import Todos from './Todos.js'
import Button from './Button.js'
import AddTodo from './AddTodo.js'

const TodoList = ({ todos, onToggle, onDelete, showAddTask, showTaskAdd, onAdd }) => {
  return(
    <div className="todo-container">
      <h1>My Todo</h1>
      <Todos 
      todos={todos} 
      onToggle={onToggle}
      onDelete={onDelete}
      />
      <Button showAddTask={showAddTask} showTaskAdd={showTaskAdd} />
     {showAddTask && <AddTodo onAdd={onAdd} />}
    </div>
    
    )
}

export default TodoList