import { FaTimes } from 'react-icons/fa'
const Todo = ({ todo, onToggle, onDelete }) => {
  
  return(
      <li className="todo" onDoubleClick={() => {onToggle(todo.id)}}>
        <div className="task">
          <h2 className={todo.status == true ? "completed" : "notCompleted"}>{todo.task}</h2>
          <p className={todo.status == true ? "completed" : "notCompleted"}>{todo.time}</p>
        </div>
        <button style={{color:'#ffffff',}} onClick={() => onDelete(todo.id)}><FaTimes /></button>
       </li>
    )
}

export default Todo