import Todo from './Todo.js'
const Todos = ({ todos, onToggle, onDelete }) => {
  return(
      <ul className="todos">
      {todos.map((todo) => (
         <Todo
         key={todo.id}
         todo={todo} 
         onToggle={onToggle} 
         onDelete={onDelete} />
      ))}
      </ul>
    )
}

export default Todos